.message {
    margin: 25px;
    display: flex;
    gap: 15px;
}
.userName {
    margin: 0 0 5px 0;
    font-weight: 500;
    cursor: pointer;
}
.userImg {
    cursor: pointer;
    display: inline-block;
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 50%;
}
